import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
export default {
	components: {
		uniSearchBar
	},
	data() {
		return {
			searchVal: ''
		}
	},
	methods: {
		search(res) {
			uni.showModal({
				content: '搜索：' + res.value,
				showCancel: false
			})
		},
		input(res) {
			this.searchVal = res.value
		},
		cancel(res) {
			uni.showModal({
				content: '点击取消，输入值为：' + res.value,
				showCancel: false
			})
		}
	}
}
